        align-self: flex-start;
        z-index: 7;
        transition: all .9s ease-in-out;
    }

    .tipo-2 .icon-burguer {
        position: absolute;
        right: 5%;
        top: 50%;
        transform: translateY(-50%);
        z-index: 10;
    }

    .tipo-2 .icon-burguer .line {
        display: block;
        width: 30px;
        height: 5px;
        background-color: #9726E0;
        margin: 5px;
        border-radius: 3px;
        transition: all .3s ease-in-out;
